.instagramLogo {
  scale: 0.75;
  margin-top: 40px;
}

ul {
  list-style: none;
}
.navBar {
  display: flex;
  flex-direction: column;
  position: sticky;
  top: 0;
  max-width: 200px;
  padding: 16px;
  max-height: 100vh;
  border-right: 1px solid rgb(235, 229, 229);
}

.profileLogo {
  width: 40px;
  background-size: 40px;
  border-radius: 20px;
  margin-right: 10px;
}

.navBarSvg {
  width: 40px;
  margin-right: 10px;
}

.actionButton {
  display: flex;
  align-items: center;
  border-radius: 10px;
  padding: 4px;
  transition: background-color 0.5s linear;
  font-size: 24px;
  margin: 20px 0;
}
.actionButton:hover {
  background-color: rgb(235, 229, 229);
}
.link:visited {
  color: black;
}
.link {
  text-decoration: none;
  cursor: pointer;
}

@media (max-width: 900px) {
  .navBar {
    flex-direction: row;
    z-index: 1;
    background-color: white;
    justify-content: space-around;
    border-radius: 0 0 12px 12px;
    min-width: 90vw;
    max-width: 90vw;
    
  }
  .instagramLogo {
    display: none;
  }
  .actionButton {
    font-size: 14px;
  }
}
